<template>
  <div
    :style="{
      position: 'absolute',
      width: val.width / w * 100 + '%',
      height: val.height / h * 100 + '%',
      left: val.left / w * 100 + '%',
      top: val.top / h * 100 + '%',
      zIndex: val.z,
      backgroundColor: val.bgColor,
      color: val.color
    }"
    contenteditable="true">
    <v-echart
      :id="chartId"
      :options="options"
      autoresize
      class="ffff"/>
  </div>
</template>

<script>
import stylec from './style.vue'
import echarts from 'echarts/lib/echarts'
import dataControl from '../../CommonModule/mixins/dataControl'
import autoToolTip from '../../CommonModule/scripts/echartsAutoToolTip.js'
const WIDGET_NAME = 'BasicPieChart'
export default {
  name: WIDGET_NAME,
  group: 'chart',
  icon: require('./icon/thumb-piechart.png'),
  title: '',
  panel: stylec,
  setting: {
    type: WIDGET_NAME,
    isContainer: false,
    isUpload: false,
    hasGuide: true,
    isChild: true,
    dragable: true,
    resizable: true,
    width: 400,
    height: 450,
    left: 400,
    top: 400,
    z: 0,
    color: '#555555',
    name: '', // , 
    desc: '', // , 
    belong: 'page',
    animationName: '',

    textStyleColor: '#666666', // 

    chartTitle: '', // 
    showTitle: false, // 
    titleColor: '#fc8700', // 
    titleFontSize: 24, // 
    titleX: 'center', // 'left''center' | 'left' | 'right' | {number}xpx
    titleY: 'top', // top'top' | 'bottom' | 'center' | {number}ypx
    titleSubText: '', // '\n'
    titleSubColor: '#999999', // 
    titleSubFontSize: 12, // 

    legendShow: true, // 
    legendWidth: '60%', // 
    legendHeight: '100', // 
    legendPositionX: 'center', // X 
    legendPositionY: 'bottom', // Y 
    legendFontSize: 12, // 
    legendTextColor: '#000000', // 
    legendIcon: 'roundRect', //  Icon  circle, rect, line, roundRect, triangle, diamond, pin, none
    legendIconWidth: 20, //  Icon 
    legendIconHeight: 10, //  Icon 
    legendIconGap: 10, //  Icon 

    seriesRadius: ['40%', '60%'], // 

    colorArr: ['#fc8700', '#ff00c0', '#f33a00', '#2cbdff', '#0000ff', '#25da29', '#f1f10e', '#07ecf8', '#af28d7'],

    showToolTip: true, // 
    autoToolTip: false, // 
    autoToolTipTime: 5000, // 

    dataAPI: 'https://easy-mock.com/mock/5cc6c0a89edd7844f38df463/cryia/api/salevolume', // API
    dataAutoRefresh: false, // 
    dataOrigin: 'local', // local  api 
    dataRefreshTime: 5, // 
    dataRefresh: false, // , dataApi

    // 
    linkEnable: false, // 
    linkIsMain: false, // 
    linkMainUUID: '', // UUID, 

    keyPrimary: 'data',
    keyTarget: 'statistics', // 
    keyXAxis: 'name', // x
    keyYAxis: 'value', // y

    staticData: {
      'code': 0,
      'data': {
        'year': 2019,
        'statistics': [
          {
            'value': 33801,
            'name': 'Samsung'
          },
          {
            'value': 63395,
            'name': 'iPhone'
          },
          {
            'value': 89297,
            'name': 'HUAWEI'
          },
          {
            'value': 76689,
            'name': 'VIVO'
          },
          {
            'value': 32219,
            'name': 'OPPO'
          },
          {
            'value': 98748,
            'name': 'MI'
          },
          {
            'value': 18290,
            'name': 'Meizu'
          }
        ]
      }
    }
  },
  mixins: [dataControl],
  props: ['w', 'h', 'val'],
  data () {
    return {
      timer: null,
      chartId: 'pie' + this.val.uuid,
      dynamicData: {}
    }
  },
  watch: {
    'val.autoToolTip': function (val) {
      this.drawBar(val ? this.val.autoToolTipTime : 0)
    },
    'val.autoToolTipTime': function (val) {
      this.drawBar(val)
    }
  },
  computed: {
    categories () {
      if (this.dynamicData[this.val.keyPrimary] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget]) {
        return this.dynamicData[this.val.keyPrimary][this.val.keyTarget].map(item => {
          return item[this.val.keyXAxis]
        })
      } else {
        return []
      }
    },
    dataSeries () {
      if (this.dynamicData[this.val.keyPrimary] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget]) {
        return this.dynamicData[this.val.keyPrimary][this.val.keyTarget].map(item => {
          return { 'value': item[this.val.keyYAxis], 'name': item[this.val.keyXAxis] }
        })
      } else {
        return []
      }
    },
    options () {
      return {
        title: {
          show: this.val.showTitle, // 
          text: this.val.chartTitle, // 
          x: this.val.titleX,
          y: this.val.titleY,
          subtext: this.val.titleSubText,
          textStyle: { // 
            fontSize: this.val.titleFontSize,
            color: this.val.titleColor,
            fontWeight: 'normal'
          },
          subtextStyle: { // 
            fontSize: this.val.titleSubFontSize,
            color: this.val.titleSubColor
          }
        },
        tooltip: {
          show: this.val.showToolTip,
          trigger: 'item',
          position: 'inside',
          formatter: '{a}<br/>{b}: {c} ({d}%)'
        },
        legend: {
          show: this.val.legendShow, // 
          x: this.val.legendPositionX,
          y: this.val.legendPositionY,
          width: this.val.legendWidth,
          // height: this.val.lengendHeight,
          icon: this.val.legendIcon, // 
          itemWidth: parseInt(this.val.legendIconWidth), // 
          itemHeight: parseInt(this.val.legendIconHeight), // 
          itemGap: parseInt(this.val.legendIconGap), // 
          textStyle: {
            color: this.val.legendTextColor,
            fontSize: this.val.legendFontSize
          },
          data: this.categories
        },
        color: this.val.colorArr,
        series: [{
          name: '',
          type: 'pie',
          radius: this.val.seriesRadius,
          center: ['50%', '50%'],
          avoidLabelOverlap: true,
          hoverAnimation: true, //  hover 
          silent: false, // 
          itemStyle: {
            normal: {
              label: {
                // textStyle: {
                //   baseline: 'middle', // 
                //   fontSize: 14,
                //   color: '#666',
                //   align: 'center' // 
                // }
              }
            }
          },
          label: {
            normal: {
              show: false,
              position: 'center'
              // formatter: function (params) {
              // return params.name + ': ' + params.value
              // }
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: '14',
                fontWeight: 'bold'
              }
            }
          },
          labelLine: {
            normal: {
              show: true
            }
          },
          data: this.dataSeries
        }]
      }
    }
  },
  mounted () {
    if (this.val.autoToolTip) {
      this.drawBar(this.val.autoToolTipTime)
    }
  },
  methods: {
    drawBar (time) {
      // domecharts
      var myChart = echarts.init(document.getElementById(this.chartId))
      // 
      myChart.setOption(this.options)
      // 
      clearInterval(this.timer)
      this.timer = autoToolTip.autoHover(myChart, this.options, this.dataSeries.length, time)
    }
  }
}
</script>
