<template>
  <div
    :style="{
      position: 'absolute',
      width: val.width / w * 100 + '%',
      height: val.height / h * 100 + '%',
      left: val.left / w * 100 + '%',
      top: val.top / h * 100 + '%',
      zIndex: val.z,
      backgroundColor: val.bgColor,
      color: val.color
    }"
    contenteditable="true">
    <v-echart
      :options="options"
      autoresize
      class="ffff"/>
  </div>
</template>

<script>
import stylec from './style.vue'
import echarts from 'echarts/lib/echarts'
import dataControl from '../../CommonModule/mixins/dataControl'

const WIDGET_NAME = 'AreaLineChart'
export default {
  name: WIDGET_NAME,
  group: 'chart',
  icon: require('./icon/thumb-linechart.png'),
  title: '',
  panel: stylec,
  setting: {
    type: WIDGET_NAME,
    isContainer: false,
    isUpload: false,
    hasGuide: true,
    isChild: true,
    dragable: true,
    resizable: true,
    width: 700,
    height: 350,
    left: 400,
    top: 400,
    z: 0,
    color: '#555555',
    name: '', // , 
    desc: '', // , 
    belong: 'page',
    animationName: '',

    chartTitle: '', // 
    textStyleColor: '#666666', // 
    itemStyleColor: '#42b983', // 
    xyturn: false, // xy
    titleColor: '#666666', // 
    xTextColor: '#666666', // X 
    yTextColor: '#666666', // Y 
    xLineColor: '#666666', // X 
    yLineColor: '#666666', // Y 
    xName: '', // x 
    yName: '', // x 
    xRotate: 0, // x 
    splitLineColor: '#3c4084', // y
    showXaxisTick: false, //  X 
    showYTick: false, //  Y 
    showXLine: true, //  X 
    showYLine: false, // Y 
    showYSplitLine: true, // Y 
    showXSplitLine: false, // X 
    showX: true, //  X 
    showY: true, //  Y 

    lgArr: [{ // 
      color: '#9e1aa6',
      offset: 0
    }, {
      color: '#0000ff',
      offset: 1
    }],
    xyArr: ['xAxis', 'yAxis'],

    gridTop: '70', // 
    gridLeft: '3%', // 
    gridRight: '8%', // 
    gridBottom: '3%', // 

    seriesLineWidth: 0, // 
    seriesSymbol: 'none', // 
    seriesSymbolSize: 5, // 
    isSmooth: true, // 
    showTooltip: false, // 

    dataAPI: 'https://easy-mock.com/mock/5cc6c0a89edd7844f38df463/cryia/api/salevolume', // API
    dataAutoRefresh: false, // 
    dataOrigin: 'local', // local  api 
    dataRefreshTime: 5, // 
    dataRefresh: false, // , dataApi

    // 
    linkEnable: false, // 
    linkIsMain: false, // 
    linkMainUUID: '', // UUID, 

    keyPrimary: 'data',
    keyTarget: 'statistics', // 
    keyXAxis: 'vendor', // x
    keyYAxis: 'sales', // y

    staticData: {
      'code': 0,
      'data': {
        'year': 2019,
        'statistics': [
          {
            'vendor': 'Samsung',
            'sales': 33801
          },
          {
            'vendor': 'iPhone',
            'sales': 63395
          },
          {
            'vendor': 'HUAWEI',
            'sales': 89297
          },
          {
            'vendor': 'VIVO',
            'sales': 76689
          },
          {
            'vendor': 'OPPO',
            'sales': 32219
          },
          {
            'vendor': 'MI',
            'sales': 98748
          },
          {
            'vendor': 'Meizu',
            'sales': 18290
          },
          {
            'vendor': '8848',
            'sales': 66282
          }
        ]
      }
    }
  },
  mixins: [dataControl],
  props: ['w', 'h', 'val'],
  data () {
    return {
      dynamicData: {}
    }
  },
  computed: {
    categories () {
      if (this.dynamicData[this.val.keyPrimary] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget]) {
        return this.dynamicData[this.val.keyPrimary][this.val.keyTarget].map(item => {
          return item[this.val.keyXAxis]
        })
      } else {
        return []
      }
    },
    dataSeries () {
      if (this.dynamicData[this.val.keyPrimary] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget]) {
        return this.dynamicData[this.val.keyPrimary][this.val.keyTarget].map(item => {
          return item[this.val.keyYAxis]
        })
      } else {
        return []
      }
    },
    xAxis () {
      return {
        name: this.val.xName,
        nameTextStyle: {
          color: this.val.xTextColor
        },
        type: 'category',
        nameLocation: 'end',
        show: this.val.showX, //  X 
        nameGap: '12',
        boundaryGap: false,
        axisTick: {
          show: this.val.showXaxisTick //  X 
        },
        axisLine: {
          show: this.val.showXLine, //  X 
          lineStyle: {
            color: this.val.xLineColor // X 
          }
        },
        splitLine: {
          show: this.val.showXSplitLine // X 
        },
        axisLabel: {
          interval: 0, // x
          rotate: this.val.xRotate, // x -90 ~ 90
          textStyle: {
            color: this.val.xTextColor // X 
          }
        },
        data: this.categories // 
      }
    },
    yAxis () {
      return { // 
        name: this.val.yName,
        nameTextStyle: {
          color: this.val.yTextColor
        },
        show: this.val.showY, //  Y 
        type: 'value',
        scale: true,
        inverse: false,
        // max: Math.max.apply(null, this.val.dataJSON.data.series),
        min: 0,
        axisLine: {
          show: this.val.showYLine, //  Y 
          lineStyle: {
            color: this.val.yLineColor // Y 
          }
        },
        axisLabel: {
          textStyle: {
            color: this.val.yTextColor // Y 
          }
        },
        axisTick: {
          show: this.val.showYTick, //  Y 
          alignWithLabel: this.val.yAxisLabel // Y 
        },
        splitLine: {
          show: this.val.showYSplitLine, //  Y 
          lineStyle: {
            type: 'solid',
            color: this.val.splitLineColor // y
          }
        },
        boundaryGap: [0.2, 0.2]
      }
    },
    options () {
      return {
        title: {
          text: this.val.chartTitle, // 
          textStyle: {
            color: this.val.titleColor
          }
        },
        tooltip: {
          show: this.val.showTooltip, // 
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
        legend: {
          data: ''
        },
        grid: {
          top: this.val.gridTop, // 
          left: this.val.gridLeft, //  
          right: this.val.gridRight, // 
          bottom: this.val.gridBottom, // 
          containLabel: true
        },
        xAxis: this.val.axisReverse ? this.yAxis : this.xAxis,
        yAxis: this.val.axisReverse ? this.xAxis : this.yAxis,
        series: [{
          name: '',
          type: 'line',
          symbol: this.val.seriesSymbol, //  'circle' | 'rectangle' | 'triangle' | 'diamond' | 'pin''arrow'
          symbolSize: this.val.seriesSymbolSize, // 
          smooth: this.val.isSmooth, // 
          itemStyle: {
            normal: {
              lineStyle: {
                width: this.val.seriesLineWidth, // 
                type: 'solid', // 
                color: this.val.lgArr[0].color // 
              }
            }
          },
          areaStyle: {
            show: false,
            normal: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, this.val.lgArr) // 
            }
          },
          // data: this.val.dataJSON.data.series
          data: this.dataSeries
        }]
      }
    }
  }
}
</script>
