<template>
  <div
    :style="{
      position: 'absolute',
      width: val.width / w * 100 + '%',
      height: val.height / h * 100 + '%',
      left: val.left / w * 100 + '%',
      top: val.top / h * 100 + '%',
      zIndex: val.z,
      color: val.color
    }"
    contenteditable="true">
    <v-echart
      :options="options"
      autoresize
      class="ffff"/>
  </div>
</template>

<script>
import stylec from './style.vue'
import dataControl from '../../CommonModule/mixins/dataControl'

const WIDGET_NAME = 'PolarBarChart'
export default {
  name: WIDGET_NAME,
  group: 'chart',
  icon: require('./icon/thumb-barpolar.png'),
  title: '',
  panel: stylec,
  setting: {
    type: WIDGET_NAME,
    isContainer: false,
    isUpload: false,
    hasGuide: true,
    isChild: true,
    dragable: true,
    resizable: true,
    width: 500,
    height: 550,
    left: 400,
    top: 400,
    z: 0,
    color: '#555555',
    name: '', // , 
    desc: '', // , 
    belong: 'page',
    animationName: '',

    showX: false, //  X 
    xLineWidth: 1, // X 
    xTextColor: '#666666', // X 
    xTextFontSize: 14, // X 
    xLabelRotate: 0, // X
    showYSplitLine: true, // Y 
    splitLineColor: '#dddddd', // y
    colorArr: ['#fc8700', '#ff00c0', '#f33a00', '#2cbdff', '#0000ff', '#25da29', '#f1f10e', '#07ecf8', '#af28d7', '#20f807'], // 
    polarSize: '70%', //   70% / 300
    splitNumber: 10, // 
    polarType: 'bar', //   line, bar

    legendShow: true, // 
    legendWidth: '60%', // 
    legendHeight: '100', // 
    legendPositionX: 'center', // X 
    legendPositionY: 'bottom', // Y 
    legendFontSize: 12, // 
    legendTextColor: '#000000', // 
    legendIcon: 'roundRect', //  Icon  circle, rect, line, roundRect, triangle, diamond, pin, none
    legendIconWidth: 20, //  Icon 
    legendIconHeight: 10, //  Icon 
    legendIconGap: 10, //  Icon 

    dataAPI: 'https://easy-mock.com/mock/5cc6c0a89edd7844f38df463/cryia/api/salevolume', // API
    dataAutoRefresh: false, // 
    dataOrigin: 'local', // local  api 
    dataRefreshTime: 5, // 
    dataRefresh: false, // , dataApi

    // 
    linkEnable: false, // 
    linkIsMain: false, // 
    linkMainUUID: '', // UUID, 

    keyPrimary: 'data',
    keyTarget: 'statistics', // 
    keyXAxis: 'vendor', // x
    keyYAxis: 'sales', // y

    staticData: {
      'code': 0,
      'data': {
        'year': 2019,
        'statistics': [
          {
            'vendor': 'Samsung',
            'sales': 33801
          },
          {
            'vendor': 'iPhone',
            'sales': 63395
          },
          {
            'vendor': 'HUAWEI',
            'sales': 89297
          },
          {
            'vendor': 'VIVO',
            'sales': 76689
          },
          {
            'vendor': 'OPPO',
            'sales': 32219
          },
          {
            'vendor': 'MI',
            'sales': 98748
          },
          {
            'vendor': 'Meizu',
            'sales': 18290
          },
          {
            'vendor': '8848',
            'sales': 66282
          }
        ]
      }
    }
  },
  mixins: [dataControl],
  props: ['w', 'h', 'val'],
  data () {
    return {
      dynamicData: {}
    }
  },
  computed: {
    legends () {
      if (this.dynamicData[this.val.keyPrimary] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget]) {
        return this.dynamicData[this.val.keyPrimary][this.val.keyTarget].map(item => {
          return item[this.val.keyXAxis]
        })
      } else {
        return []
      }
    },
    dataSeries () {
      let seriesData = []

      if (this.dynamicData[this.val.keyPrimary]) {
        const origin = this.dynamicData[this.val.keyPrimary][this.val.keyTarget] || []
        const legends = this.legends

        for (let i in origin) {
          let item = {}
          item.type = this.val.polarType
          item.data = origin.map((value, index) => {
            if (index === Number(i)) {
              return value[this.val.keyYAxis]
            } else {
              return 0
            }
          })
          item.coordinateSystem = 'polar'
          item.name = legends[i]
          item.stack = 'a'

          seriesData[i] = item
        }
      }

      return seriesData
    },
    options () {
      return {
        angleAxis: {
          type: 'category',
          z: 1,
          axisLine: {
            show: false
          }
        },
        color: this.val.colorArr,
        radiusAxis: {
          axisLine: { //  
            show: this.val.showX, // 
            lineStyle: {
              color: this.val.xLineColor, // 'rgb(128, 128, 128)''rgba(128, 128, 128, 0.5)'
              type: 'solid', // soliddasheddotted
              width: this.val.xLineWidth // 
            }
          },
          axisTick: { // 
            show: false
          },
          axisLabel: { // 
            show: this.val.showXaxisTick, //  X 
            margin: 8,
            inside: false,
            rotate: this.val.xLabelRotate, // 
            formatter: function (value, index) { // 
              return value + ''
            },
            textStyle: {
              color: this.val.xTextColor, // X 
              fontSize: this.val.xTextFontSize // X 
            }
          },
          max: function (value) {
            return Math.floor(value.max / 1)
          },
          splitLine: {
            show: this.val.showYSplitLine, // 
            interval: 'auto',
            lineStyle: {
              interval: 1,
              color: this.val.splitLineColor
            }
          },
          splitNumber: this.val.splitNumber,
          splitArea: {
            show: false
          }
        },
        polar: {
          center: ['50%', '50%'],
          radius: this.val.polarSize //  70% / 200
        },
        series: this.dataSeries,
        legend: {
          show: this.val.legendShow, // 
          x: this.val.legendPositionX,
          y: this.val.legendPositionY,
          width: this.val.legendWidth,
          // height: this.val.lengendHeight,
          icon: this.val.legendIcon, // 
          itemWidth: parseInt(this.val.legendIconWidth), // 
          itemHeight: parseInt(this.val.legendIconHeight), // 
          itemGap: parseInt(this.val.legendIconGap), // 
          textStyle: {
            color: this.val.legendTextColor,
            fontSize: this.val.legendFontSize
          },
          data: this.legends
        },
        tooltip: {
          trigger: 'axis'
        }
      }
    }
  }
}
</script>
