<template>
  <div
    :style="{
      position: 'absolute',
      width: val.width / w * 100 + '%',
      height: val.height / h * 100 + '%',
      left: val.left / w * 100 + '%',
      top: val.top / h * 100 + '%',
      zIndex: val.z,
      backgroundColor: val.bgColor,
      color: val.color
    }"
    contenteditable="true">
    <v-echart
      :id="chartId"
      :options="options"
      autoresize
      class="ffff"/>
  </div>
</template>

<script>
import stylec from './style.vue'
import echarts from 'echarts/lib/echarts'
import dataControl from '../../CommonModule/mixins/dataControl'
import autoToolTip from '../../CommonModule/scripts/echartsAutoToolTip.js'
const WIDGET_NAME = 'BasicBarChart'
export default {
  name: WIDGET_NAME,
  group: 'chart',
  icon: require('./icon/thumb-barchart.png'),
  title: '',
  panel: stylec,
  setting: {
    type: WIDGET_NAME,
    isContainer: false,
    isUpload: false,
    hasGuide: true,
    isChild: true,
    dragable: true,
    resizable: true,
    width: 700,
    height: 350,
    left: 400,
    top: 400,
    z: 0,
    color: '#555555',
    name: '', // , 
    desc: '', // , 
    belong: 'page',
    animationName: '',

    chartTitle: '', // 
    textStyleColor: '#666666', // 
    itemStyleColor: '#42b983', // 
    axisReverse: false, // xy
    titleColor: '#666666', // 
    xTextColor: '#666666', // X 
    yTextColor: '#666666', // Y 
    xLineColor: '#666666', // X 
    xName: '', // X  (x )
    yName: '', // Y  (y )
    xRotate: 0, // X 
    yLineColor: '#666666', // Y 
    yInverse: false, // Y 
    splitLineColor: '#3c4084', // y
    showXaxisTick: false, //  X 
    showYTick: false, //  Y 
    xAxisLabel: false, // X 
    showXLine: true, //  X 
    showYLine: false, // Y 
    showYSplitLine: true, // Y 
    showXSplitLine: false, // X 
    showX: true, //  X 
    showY: true, //  Y 
    showTitle: false, // 
    showToolTip: true, // 

    seriseBarWidth: '40%', // 
    seriseRadius1: 20, // 
    seriseRadius2: 20, // 
    seriseRadius3: 0, // 
    seriseRadius4: 0, // 
    seriseRadiuses: [0, 0, 0, 0],
    lgArr: [{ // 
      color: '#ba3ba3',
      offset: 0
    }],

    gridTop: '70', // 
    gridLeft: '3%', // 
    gridRight: '8%', // 
    gridBottom: '3%', // 

    autoToolTip: false, // 
    autoToolTipTime: 5000, // 

    dataAPI: 'https://easy-mock.com/mock/5cc6c0a89edd7844f38df463/cryia/api/salevolume', // API
    dataAutoRefresh: false, // 
    dataOrigin: 'local', // local  api 
    dataRefreshTime: 5, // 
    dataRefresh: false, // , dataApi

    // 
    linkEnable: false, // 
    linkIsMain: false, // 
    linkMainUUID: '', // UUID, 

    keyPrimary: 'data',
    keyTarget: 'statistics', // 
    keyXAxis: 'vendor', // x
    keyYAxis: 'sales', // y

    staticData: {
      'code': 0,
      'data': {
        'year': 2019,
        'statistics': [
          {
            'vendor': 'Samsung',
            'sales': 33801
          },
          {
            'vendor': 'iPhone',
            'sales': 63395
          },
          {
            'vendor': 'HUAWEI',
            'sales': 89297
          },
          {
            'vendor': 'VIVO',
            'sales': 76689
          },
          {
            'vendor': 'OPPO',
            'sales': 32219
          },
          {
            'vendor': 'MI',
            'sales': 98748
          },
          {
            'vendor': 'Meizu',
            'sales': 18290
          }
        ]
      }
    }
  },
  mixins: [dataControl],
  props: ['w', 'h', 'val'],
  data () {
    return {
      timer: null,
      chartId: 'bar' + this.val.uuid,
      dynamicData: {}
    }
  },
  watch: {
    'val.seriseRadiuses': function (val) {
      console.log(val, typeof this.val.seriseRadiuses)
    },
    'val.autoToolTip': function (val) {
      this.drawBar(val ? this.val.autoToolTipTime : 0)
    },
    'val.autoToolTipTime': function (val) {
      this.drawBar(val)
    }
  },
  computed: {
    categories () {
      if (this.dynamicData[this.val.keyPrimary] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget]) {
        return this.dynamicData[this.val.keyPrimary][this.val.keyTarget].map(item => {
          return item[this.val.keyXAxis]
        })
      } else {
        return []
      }
    },
    dataSeries () {
      if (this.dynamicData[this.val.keyPrimary] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget]) {
        return this.dynamicData[this.val.keyPrimary][this.val.keyTarget].map(item => {
          return item[this.val.keyYAxis]
        })
      } else {
        return []
      }
    },
    xAxis () {
      return {
        name: this.val.xName, // x x
        nameTextStyle: {
          color: this.val.xTextColor
        },
        // nameLocation: 'end',
        show: this.val.showX, //  X 
        nameGap: '6', // x 
        boundaryGap: true,
        axisTick: {
          show: this.val.showXaxisTick, //  X 
          alignWithLabel: this.val.xAxisLabel // X 
        },
        axisLine: {
          show: this.val.showXLine, //  X 
          lineStyle: {
            color: this.val.xLineColor // X 
          }
        },
        splitLine: {
          show: this.val.showXSplitLine // X 
        },
        axisLabel: {
          interval: 0, // x
          rotate: this.val.xRotate, // x
          textStyle: {
            color: this.val.xTextColor // X 
          }
        },
        position: this.val.yInverse ? 'top' : '',
        data: this.categories // 
      }
    },
    yAxis () {
      return { // 
        show: this.val.showY, //  Y 
        type: 'value',
        name: this.val.yName,
        nameTextStyle: {
          color: this.val.yTextColor
        },
        scale: true,
        inverse: this.val.yInverse, // y 
        // max: Math.max.apply(null, this.val.dataJSON.series),
        min: 0,
        axisLine: {
          show: this.val.showYLine, //  Y 
          lineStyle: {
            color: this.val.yLineColor // Y 
          }
        },
        axisLabel: {
          textStyle: {
            color: this.val.yTextColor // Y 
          }
        },
        axisTick: {
          show: this.val.showYTick, //  Y 
          alignWithLabel: this.val.yAxisLabel // Y 
        },
        splitLine: {
          show: this.val.showYSplitLine, //  Y 
          lineStyle: {
            type: 'solid',
            color: this.val.splitLineColor // y
          }
        },
        boundaryGap: [0.2, 0.2]
      }
    },
    options () {
      return {
        title: {
          show: this.val.showTitle, // 
          text: this.val.chartTitle, // 
          textStyle: {
            color: this.val.titleColor
          }
        },
        tooltip: {
          show: this.val.showToolTip,
          trigger: 'axis', //  axis | item
          axisPointer: {
            type: 'shadow' //  line | shadow
          }
        },
        grid: {
          top: this.val.gridTop, // 
          left: this.val.gridLeft, //  
          right: this.val.gridRight, // 
          bottom: this.val.gridBottom, // 
          containLabel: true
        },
        xAxis: this.val.axisReverse ? this.yAxis : this.xAxis,
        yAxis: this.val.axisReverse ? this.xAxis : this.yAxis,
        series: [{
          name: '',
          type: 'bar',
          barWidth: this.val.seriseBarWidth, // 
          itemStyle: {
            normal: {
              barBorderRadius: [this.val.seriseRadiuses[0] || this.val.seriseRadiuses[0] + 1, this.val.seriseRadiuses[1] || this.val.seriseRadiuses[1] + 1, this.val.seriseRadiuses[2] || this.val.seriseRadiuses[2] + 1, this.val.seriseRadiuses[3] || this.val.seriseRadiuses[3] + 1], // 
              // barBorderRadius: this.val.seriseRadiuses || [0, 0, 0, 0], // 
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, this.val.lgArr) // /
            }
          },
          data: this.dataSeries
        }]
      }
    }
  },
  mounted () {
    if (this.val.autoToolTip) {
      this.drawBar(this.val.autoToolTipTime)
    }
  },
  methods: {
    drawBar (time) {
      // domecharts
      var myChart = echarts.init(document.getElementById(this.chartId))
      // 
      myChart.setOption(this.options)
      // 
      clearInterval(this.timer)
      this.timer = autoToolTip.autoHover(myChart, this.options, this.dataSeries.length, time)
    }
  }
}
</script>
