<template>
  <div
    :style="{
      position: 'absolute',
      width: val.width / w * 100 + '%',
      height: val.height / h * 100 + '%',
      left: val.left / w * 100 + '%',
      top: val.top / h * 100 + '%',
      zIndex: val.z,
      backgroundColor: val.bgColor,
      color: val.color
    }"
    contenteditable="true">
    <v-echart
      :id="chartId"
      :options="options"
      autoresize
      class="ffff"/>
  </div>
</template>

<script>
import stylec from './style.vue'
import dataControl from '../../CommonModule/mixins/dataControl'

import 'echarts/map/js/china.js'
import echarts from 'echarts'
import autoToolTip from '../../CommonModule/scripts/echartsAutoToolTip.js'
const WIDGET_NAME = 'BraidChinaMap'
export default {
  name: WIDGET_NAME,
  group: 'map',
  icon: require('./icon/thumb-chartMap.png'),
  title: '',
  panel: stylec,
  setting: {
    type: WIDGET_NAME,
    isContainer: false,
    isUpload: false,
    hasGuide: true,
    isChild: true,
    dragable: true,
    resizable: true,
    width: 500,
    height: 400,
    left: 400,
    top: 400,
    z: 0,
    color: '#555555',
    name: '', // , 
    desc: '', // , 
    belong: 'page',
    animationName: '',

    chartTitle: '', // 
    titleColor: '#666666', // 

    colorArr: ['#1ccada', '#d2f4f8'],
    bgMapColor: '', // 
    mapBorderColor: '#ffffff', // 
    mapEmphasisAreaColor: 'rgba(231,235,13,0.9)', // 
    showVisualMap: true, // 
    visualTextColor: '#aaaaaa', // 
    autoToolTip: false, // 
    autoToolTipTime: 5000, // 

    dataAPI: 'https://mock.kunteng.org.cn/mock/5ca2cba34918866472494a14/chartMap', // API
    dataAutoRefresh: false, // 
    dataOrigin: 'local', // local  api 
    dataRefreshTime: 5, // 
    dataRefresh: false, // , dataApi

    // 
    linkEnable: false, // 
    linkIsMain: false, // 
    linkMainUUID: '', // UUID, 

    keyPrimary: 'data',
    keyTarget: 'china', // 
    keyXAxis: '', // x
    keyYAxis: '', // y

    staticData: {
      'code': 0,
      'data': {
        'year': 2019,
        'china': [
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) },
          { name: '', value: Math.round(Math.random() * 100) }
        ]
      }
    }
  },
  mixins: [dataControl],
  props: ['w', 'h', 'val'],
  data () {
    return {
      timer: null,
      chartId: 'char' + this.val.uuid,
      dynamicData: {}
    }
  },
  computed: {
    dataSeries () {
      if (this.dynamicData[this.val.keyPrimary] && this.dynamicData[this.val.keyPrimary][this.val.keyTarget]) {
        return this.dynamicData[this.val.keyPrimary][this.val.keyTarget]
      } else {
        return []
      }
    },
    options () {
      return {
        backgroundColor: this.val.bgMapColor, // 
        title: {
          text: this.val.chartTitle, // 
          textStyle: {
            color: this.val.titleColor
          }
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b}{c}'
        },
        roamController: {
          show: true,
          x: 'right',
          mapTypeControl: {
            'china': true
          }
        },
        visualMap: {
          show: this.val.showVisualMap, // 
          type: 'continuous', // 
          min: 0,
          max: 100, // 
          left: 'left',
          top: 'bottom',
          color: this.val.colorArr,
          textStyle: {
            color: this.val.visualTextColor // 
          },
          calculable: true // 
        },
        series: [{
          name: '',
          type: 'map',
          mapType: 'china',
          roam: false,
          itemStyle: {
            normal: { // 
              areaColor: this.val.colorArr[1],
              borderColor: this.val.mapBorderColor, // 
              textStyle: {
                color: 'red'
              }
            },
            emphasis: { // 
              areaColor: this.val.mapEmphasisAreaColor
            }
          },
          data: this.dataSeries
        }, {
          name: '',
          type: 'effectScatter',
          coordinateSystem: 'geo',
          data: [
            { name: '', value: [117.27, 31.86, 56] },
            { name: '', value: [129.58, 44.6, 94] },
            { name: '', value: [86.06, 41.68, 150] }
          ],
          encode: {
            value: 2
          },
          symbolSize: function (val) {
            return val[2] / 10
          },
          showEffectOn: 'render',
          rippleEffect: {
            brushType: 'stroke'
          },
          hoverAnimation: true,
          label: {
            normal: {
              formatter: '{b}',
              position: 'right',
              show: true
            }
          },
          itemStyle: {
            normal: {
              color: '#f4e925',
              shadowBlur: 10,
              shadowColor: '#333'
            }
          },
          zlevel: 1
        }]
      }
    }
  },
  watch: {
    'val.autoToolTip': function (val) {
      this.drawBar(val ? this.val.autoToolTipTime : 0)
    },
    'val.autoToolTipTime': function (val) {
      this.drawBar(val)
    }
  },
  mounted () {
    if (this.val.autoToolTip) {
      this.drawBar(this.val.autoToolTipTime)
    }
  },
  methods: {
    drawBar (time) {
      // domecharts
      var myChart = echarts.init(document.getElementById(this.chartId))
      // 
      myChart.setOption(this.options)
      // 
      clearInterval(this.timer)
      this.timer = autoToolTip.autoHover(myChart, this.options, this.dataSeries.length, time)
    }
  }
}
</script>
